* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    
}
html {
    font-family: Arial, Helvetica, sans-serif
}
:root {
    --width-header: 80px;
}
a {
    text-decoration: none;
}
@keyframes fadein {
    to {
      transform: rotate(360deg);
    }
}

